/** CellBuilder **/

class GridColDefRole extends GridColDef {
	constructor(public roleDef: { role: string, name: string }, roleUiHandler?: RoleEditUiHandler<IRoleEditUiHandlerContext> & RoleUiHandlerContextUsed) {
		super(roleDef.role);
		this.setLabel(roleDef.name || roleDef.role);
		this.setDescription(roleDef.name || roleDef.role);
		this.setCellBuilder(new CellBuilderRole(roleDef.role, roleUiHandler));
		this.setFlex('2rem', 1, 1)
		this.setMaxWidth('15rem');
	}

	isFlagDisabledCol(root: HTMLElement): boolean {
		const grid = DOMSH.findHost<Grid>(root);
		const gridDisabledRoles = new Set(grid.getAttribute("data-roles-disabled")?.split(" "));
		return gridDisabledRoles.has(this.roleDef.role);
	}

	hasOtherFlagDisabledCol(root: HTMLElement): boolean {
		const grid = DOMSH.findHost<Grid>(root);
		const gridDisabledRoles = new Set(grid.getAttribute("data-roles-disabled")?.split(" "));
		const headerDiv = root.parentElement;
		if (Array.from(headerDiv.childNodes).find((cell: HTMLDivElement) => {
			const role = cell.getAttribute("data-role");
			if (role && role != this.roleDef.role) {
				if (gridDisabledRoles.has(role)) return true;
			}
		})) return true;
	}


	setFlagDisabledCol(root: HTMLElement, forcedDisabled: boolean | 'toogle' = 'toogle', forcedDisabledOnOtherCols?: boolean | 'toogle') {
		const grid = DOMSH.findHost<Grid>(root);
		const gridDisabledRoles = new Set(grid.getAttribute("data-roles-disabled")?.split(" "));
		const cellNewSateIsDisabled = forcedDisabled == "toogle" ? !this.isFlagDisabledCol(root) : forcedDisabled;

		if (cellNewSateIsDisabled) gridDisabledRoles.add(this.roleDef.role);
		else gridDisabledRoles.delete(this.roleDef.role);

		if (forcedDisabledOnOtherCols != null) {
			const headerDiv = root.parentElement;
			headerDiv.childNodes.forEach((cell: HTMLDivElement) => {
				const role = cell.getAttribute("data-role");
				if (role && role != this.roleDef.role) {
					if (forcedDisabledOnOtherCols == "toogle") forcedDisabledOnOtherCols = !cellNewSateIsDisabled;
					if (forcedDisabledOnOtherCols) gridDisabledRoles.add(role);
					else gridDisabledRoles.delete(role);
				}
			});

		}

		DOM.setAttr(grid, "data-roles-disabled", Array.from(gridDisabledRoles).join(" "));
	}

	buildColHeader(col: GridCol, root: HTMLElement) {
		const gridDisabledRoles = new Set(col.grid.getAttribute("data-roles-disabled")?.split(" "));

		DOM.setAttrBool(root, "disabled", gridDisabledRoles?.has(this.roleDef.role));

		col.rootHeader = root;
		DOM.setAttr(root, "data-role", this.roleDef.role);
		const roleGrp = ROLES.extractGroupFromRole(this.roleDef.role);
		let title = `Rôle : ${this.roleDef.name}`;
		if (roleGrp) {
			const grpDef = ROLES.findGroupDef(roleGrp);
			title = `Groupe exclusif : ${grpDef.label}\n${title}`;
			DOM.setAttr(root, "data-role-group", roleGrp);
			const grpElt = root.appendChild(<div class="grp">{grpDef.label ? grpDef.label : ""}</div>);
			if (grpDef.color) grpElt.style.backgroundColor = grpDef.color;
		}

		const btn = root.appendChild(
			<c-button class="colBody roleName"
								label={this.getLabel(col)}
								ui-context="custom"
								onclick={(ev: MouseEvent) => {
									if (ev.ctrlKey)
										this.setFlagDisabledCol(root, false);
									else {
										if (!this.isFlagDisabledCol(root) && this.hasOtherFlagDisabledCol(root))
											this.setFlagDisabledCol(root, false, false);
										else
											this.setFlagDisabledCol(root, false, true);
									}
									ev.stopPropagation();
								}}/>) as Button;

		root.title = title;
	}
}

class CellBuilderRole extends CellBuilderFlagIcon {

	roleRegexp: RegExp;

	constructor(protected role: string, protected roleUiHandler?: RoleEditUiHandler<IRoleEditUiHandlerContext> & RoleUiHandlerContextUsed) {
		super(role);
	}

	redrawCell(row: GridDataRowJson<JRoleExpEntrySetAccount>, root: HTMLElement): void {
		const grid = DOMSH.findHost<Grid>(root);
		super.redrawCell(row, root);
		DOM.addClass(root, "role");
		DOM.setAttrBool(root, "disabled", grid.getAttribute("data-roles-disabled")?.split(" ").indexOf(this.role) > -1);
		const isRolesUiHandlerUsable = RolesExplorerTabBase.isRolesUiHandlerUsableFor(this.roleUiHandler, row.rowDatas);
		DOM.setAttrBool(root, "unusedForLevel", !isRolesUiHandlerUsable);
		root.title = !isRolesUiHandlerUsable ? "Rôle non exploité sur ce niveau" : "";

	}

	_getValue(row: GridDataRowJson<JRoleExpEntrySetAccount>): string {
		if (this.role) {
			if (row.rowDatas.denied?.indexOf(this.role) > -1)
				return "Rôle retiré";
			if (row.rowDatas.allowed?.indexOf(this.role) > -1)
				return "Rôle affecté";
		}
		return "";
	}

	protected _getIcon(row: GridDataRowJson<JRoleExpEntrySetAccount>): string {
		if (this.role) {
			if (row.rowDatas.denied?.indexOf(this.role) > -1)
				return "/@skin@/core/dialogs/rolesExplorer/removed.svg";
			if (row.rowDatas.allowed?.indexOf(this.role) > -1)
				return "/@skin@/core/dialogs/rolesExplorer/added.svg";
			return "";
		}
	}
}

class CellBuilderMainRoleLevelEntry extends CellBuilderIconLabel {
	cacheKey: symbol;

	constructor(protected reg: IReg<ICoreUniverseEnv>, dataKey: string) {
		super(dataKey);
		this.iconWidth = "1.3rem";
	}

	protected _getIcon(row: GridDataRowJson<JRoleExpEntryStructLevel<any> | JRoleExpEntrySetAccount>): string {
		if ("structLevel" in (row.rowDatas as JRoleExpEntryStructLevel<any>))
			return `--roles-struct-level-${(row.rowDatas as JRoleExpEntryStructLevel<any>).structLevel}`
	}

	redrawCell(row: GridDataRowJson<JRoleExpEntryStructLevel<any> | JRoleExpEntrySetAccount>, root: HTMLElement): void {
		super.redrawCell(row, root);

		const span = root.firstElementChild as HTMLElement;
		let userRef: UserRef = span.nextElementSibling as UserRef;
		if ("account" in (row.rowDatas as JRoleExpEntrySetAccount)) {
			if (!userRef)
				userRef = root.appendChild(<UserRef class="inline list" î={{reg: this.reg, withIcon: true, skinOver: "rolesExplorer/userRef"} as OUserRefInit}/>) as UserRef;
			userRef.nickOrAccount = row.getData("account");
			DOM.setHidden(userRef, false);
			DOM.setHidden(span, true);
		} else {
			if (userRef) DOM.setHidden(userRef, true);
			DOM.setHidden(span, false);
		}
	}
}

