/** Actions **/

/** RAZ filtres */
const actionRazFilters = new Action<IUsersRolesActionCtx<any, any, any>>("razFilters")
	.setLabel("Réinitialiser les filtres")
	.setGroup("filters")
	.setIcon("/@skin@/commons/icons/reset-filter.svg")
	.setEnabled((ctx: IUsersRolesActionCtx<any, any, any>) => {
		return ctx.rolesExplorer.isFiltered;
	})
	.setExecute((ctx: IUsersRolesActionCtx<any, any, any>, ev: Event) => {
		ctx.rolesExplorer.razFilters();
	});

/** Reload UI */
const actionRefreshUi = new Action<IUsersRolesActionCtx<any, any, any>>("refreshUi")
	.setLabel("Rafraichir la liste des rôles")
	.setGroup("reload")
	.setIcon("/@skin@/commons/icons/refresh.svg")
	.setExecute((ctx: IUsersRolesActionCtx<any, any, any>, ev: Event) => {
		ctx.rolesExplorer.refresh();
	});

/** Wrapper d'une action qui déclenche un refresh du tree
 * FIXME : usage pas optimal... passer plutot par des remontées d'event de modif sur les objets concernés */
export class ActionWrapperRefresRolesExplorer<C extends IUsersRolesActionCtx<any, any, any>> extends ActionWrapper<C> {

	isEnabled(ctx: C): boolean {
		return super.isEnabled(ctx);
	}

	async execute(ctx: C, ev?: Event): Promise<any> {
		let result = await super.execute(ctx, ev);
		await ctx.rolesExplorer.refresh();
		return result;
	}
}

